@import url("./../criticalStyles/globalStyle.css");

.brand{
    position: relative;
    flex-grow: 2;

    .brush-stroke{
        position: relative;
        height: 75vh ;
        transform:translateY(-75vh);
        z-index: 1;
    }

    .brush-overlay{
        position: absolute;
        height: 75vh;
        width: 100%;
        background-color: white;
        z-index: 2;
        top: 0px; 
        filter: blur(1px);
    }

    .brush-art{
        animation: paint 1.8s cubic-bezier(0.69, 0.13, 0.21, 0.91) forwards;
    }

    .brand-logo{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 10;
        text-align: center;
        transform:translateY(-50%);
        -webkit-transition: opacity 1.6s linear ;
        -moz-transition: opacity 1.6s linear ;
        transition: opacity 1.6s linear ;

        img{
            width: 90%;
            height: auto;
            position: relative;
            top:50%;
        }
    }
    
}

.cover-page{
	display: flex;
	flex-flow: column nowrap;
    align-items: center;
    overflow: hidden;
    height: 100vh;
    width: 100vw;

    nav{
        -webkit-transition: opacity 1.6s linear 0.7s;
        -moz-transition: opacity 1.6s linear 0.7s;
        transition: opacity 1.6s linear 0.7s;
    }
     
    .navigation--site-links{
    
        &:hover{
            color: white;
            background-color: $gold;
        }

        a{
            display: block;
            padding: 12px 0;
            
        }
    }
}


.navigation--social{
    justify-content:center;

    li:not(:last-child){
        padding-right:27px;
    }

    .social-icon{
        width:30px;
        height:30px;
    } 
}

/* animations */
@keyframes paint{
    0%{
       top:-75vh; 
    }
    
    100%{
        transform: translateY(0)
    }
}

.reveal{
    opacity: 1;
}

.hide{
    opacity: 0;
}
